import { useRef, useEffect, useState } from "react";
import InfoIcon from "../Tooltip/InfoIcon";
import VideoPopup from "../VideoPopup/VideoPopup";
export default function ItemCodeInput({
  customClass,
  label,
  inputVal,
  setInputVal,
  important,
  id = "",
  maxLength,
  inputRef,
  handleBlur,
  customInputClass,
  placeholder = "",
  showInfoIcon = true,
  showHelpVideo = false,
}) {
  const internalRef = useRef(null);
  useEffect(() => {
    if (inputRef) {
      inputRef.current = internalRef.current;
    }
  }, [inputRef]);
  const [showVideo, setShowVideo] = useState(false);
  const [videoToPlay, setVideoToPlay] = useState("");

  return (
    <div
      className={`form-group ${
        customClass == null ? "SearchField" : `${customClass}`
      }`}
    >
      <div className="inputContainer">
        <div className="inputLabel">{label}</div>
        <input
          ref={internalRef}
          onClick={(e) => e.target.select()}
          id={`${id}`}
          type="text"
          className={`form-control item_code_input candela_input ${customInputClass}`}
          value={inputVal}
          onChange={(e) => {
            const value = e.target.value.replace(/[^a-zA-Z0-9 ]/g, "");
            setInputVal(value);
          }}
          style={important == "true" ? { backgroundColor: "#FFFFE8" } : {}}
          maxLength={`${maxLength}`}
          onBlur={handleBlur}
          placeholder={placeholder}
        />
        {showInfoIcon && (
          <span className="itemCodeInfoIcon">
            <InfoIcon
              body={
                showHelpVideo ? (
                  <>
                    {`Item Code can be Numeric or Alpha-Numeric. Max length of the Item Code is defined in "System Configuration > Item Tab. If Item coding template is defined, for selected department, then new item code can be generated by pressing 'C' Button.`}
                    <br />
                    <span>
                      For further details,{" "}
                      <a
                        href="#"
                        onClick={() => {
                          setShowVideo(true);
                          setVideoToPlay(
                            "https://youtu.be/iuH2IyC2mTY?list=PLcoOaKB-DtArZ5XmO-ggJpdDFegjZfCTm"
                          );
                        }}
                      >
                        See this video
                      </a>
                    </span>
                  </>
                ) : (
                  "Item Code can be Numeric or Alpha-Numeric. Max length of the Item Code is defined in 'System Configuration > Item Tab'. If Item coding template is defined, for selected department, then new item code can be generated by pressing 'C' Button."
                )
              }
            />
          </span>
        )}
      </div>
      {showVideo && (
        <VideoPopup handleClose={setShowVideo} youTubeLink={videoToPlay} />
      )}
    </div>
  );
}
