import React, { useEffect, useState } from "react";
import { MdInfoOutline } from "react-icons/md";
import RightDrawer from "../RightDrawer/RightDrawer";
import Tooltip from "@mui/material/Tooltip";
import CustomTooltip from "../Tooltip/CustomTooltip";
import RelatedOperationsMenu from "../RelatedOperation/RelatedOperation";
import InfoIcon from "../Tooltip/InfoIcon";

export default function Tabs({
  tab1name,
  isRecordTab,
  tab2name,
  setMode,
  setIsRecordTab,
  mode,
  setResetState,
  resetState,
  handleEditClick,
  articleModel = [],
  videoModel = [],
  showYouTubeVideo = true,
  showRelatedOperation = true,
  menuItems = [],
  tooltipText = null,
  hideNewBtn = true,
  customLabel = "",
  editBtnName = "Edit",
  importButtonLink = "",
  editBtnInfoText = "",
  customEditLabel = "",
  editInfoPalacement = "top",
  showEditBtn = true
}) {
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);
  useEffect(() => {
    if (isDrawerOpen) {
      document.body.classList.add("drawer-open");
    } else {
      document.body.classList.remove("drawer-open");
    }

    // Cleanup on unmount
    return () => {
      document.body.classList.remove("drawer-open");
    };
  }, [isDrawerOpen]);
  const handleDrawerToggle = () => {
    setIsDrawerOpen(!isDrawerOpen);
  };

  const handleDrawerClose = () => {
    setIsDrawerOpen(false);
  };
  return (
    <>
      <ul
        className="nav nav-tabs col-md-11 col-sm-11"
        id="myTab"
        role="tablist"
      >
        <li className="modeBtnContainer">
          {importButtonLink != "" && mode.toLowerCase() == "new" && (
            <a
              style={{
                color: "#007bff",
                fontWeight: "bold",
                textDecoration: "underline",
                marginRight: "10px",
              }}
              href={importButtonLink}
              target="_blank"
            >
              Import Customers
            </a>
          )}

          {hideNewBtn && (
            <CustomTooltip
              body="Create New Record"
              showOnMobile={false}
              placement="bottom"
            >
              <button
                style={{
                  borderRadius: (isRecordTab && showEditBtn)
                    ? "30px 0px 0px 30px"
                    : "30px 30px 30px 30px",
                }}
                className="page_add_btn"
                onClick={() => {
                  setMode("new");
                  setResetState(!resetState);
                  setIsRecordTab(false);
                }}
              >
                <img
                  src="data:image/png;base64,"
                  width="15"
                  height="15"
                  alt=""
                />
                New
              </button>
            </CustomTooltip>
          )}
          {isRecordTab && showEditBtn && (
            <div className="d-flex align-items-center">
            <CustomTooltip
              body="Modify Selected Record"
              showOnMobile={false}
              placement="bottom"
            >
              <button
                className="page_edit_btn"
                onClick={() => {
                  handleEditClick();
                }}
                style={{
                  borderRadius: hideNewBtn
                    ? "0px 30px 30px 0px"
                    : "30px 30px 30px 30px",
                }}
              >
                <img
                  src="data:image/png;base64,"
                  width="15"
                  height="15"
                  alt=""
                />
                {editBtnName}
              </button>
            </CustomTooltip>
            {editBtnInfoText != "" && <InfoIcon body={editBtnInfoText} placement={editInfoPalacement} /> } 
            </div>            
            
          )}
          {showRelatedOperation && (
            <RelatedOperationsMenu menuItems={menuItems} />
          )}
          {showYouTubeVideo && (
            <Tooltip title="Videos and Help Documents" placement="top">
              <span className="yotubeIcon" onClick={handleDrawerToggle}>
                <span className="youTubeBtnText">Learn</span>
                <img
                  src="data:image/png;base64,"
                  alt="youTube"
                />
              </span>
            </Tooltip>
          )}
        </li>
        <li className="nav-item">
          <a
            className={`nav-link ${isRecordTab ? "tabactive active" : ""}`}
            data-toggle="tab"
            href="#tabs-2"
            role="tab"
            id="recordsTab"
            onClick={() => {
              setIsRecordTab(true);
              setMode("");
            }}
          >
            {tab1name || "Records"}
            {tooltipText != "" && tooltipText != null && (
              <span style={{ marginLeft: "8px" }}>
                <CustomTooltip placement="right" body={tooltipText}>
                  <MdInfoOutline size={20} />
                </CustomTooltip>
              </span>
            )}
          </a>
        </li>
        <li className="nav-item">
          <a
            className={`nav-link ${!isRecordTab ? "tabactive active" : ""}`}
            data-toggle="tab"
            href="#tabs-1"
            role="tab"
            id="grn"
            onClick={() => {
              setIsRecordTab(false);
              setMode("new");
            }}
          >
            {tab2name || "Defintion"}
          </a>
        </li>
        {!isRecordTab && (
          <li
            className={`${
              mode.toLowerCase() == "viewmode" ? "viewModeLabel" : "modeLabel"
            }`}
            style={{
              color:
                mode.toLowerCase() == "new"
                  ? customLabel.trim() == ""
                    ? "rgb(39, 174, 96)"
                    : "#C52424"
                  : mode.toLowerCase() == "viewmode" ||
                    mode.toLowerCase() == "poapproved"
                  ? "red"
                  : "rgb(247, 148, 29)",
            }}
          >
            {mode.toLowerCase() == "new"
              ? customLabel.trim() == ""
                ? "Add New Record"
                : customLabel
              : mode.toLowerCase() == "viewmode"
              ? "View mode only"
              : mode.toLowerCase() == "poapproved"
              ? "Approved PO cannot be Updated"
              : customEditLabel != "" ? customEditLabel : "Edit Existing Record"}
          </li>
        )}
      </ul>

      {/* Right Drawer */}
      <RightDrawer
        isOpen={isDrawerOpen}
        onClose={handleDrawerClose}
        videoModel={videoModel}
        articleModel={articleModel}
        setIsOpen={setIsDrawerOpen}
      ></RightDrawer>
    </>
  );
}
