import { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { FaAngleDoubleRight } from "react-icons/fa";
import { ImExit } from "react-icons/im";
import { FaSearch } from "react-icons/fa";
import { LuMenu } from "react-icons/lu";
import { motion} from "framer-motion";

export default function LeftMenuTop({handleNavigation,handleLogOut}) {
  let screenName = useSelector(state => {
    return state.breadCrumb
  })
  const [breadCrumb,setBreadCrumb] = useState("")
  useEffect(()=>{
    setBreadCrumb(screenName)
  },[screenName])
  return (
    <>
    <div className="scoop-left-header">
          <motion.div className="scoop-logo" >
            <img
              onClick={handleNavigation}
              src="data:image/png;base64,"
              alt=""
              id="nimbusLogo"
            />
             <FaSearch id="searchIcon" />
            <input
              type="text"
              className="form-control btnClickTrackCls"
              placeholder="Search (^+K)"
              id="searchInput"
            />
            <i className="fa fa-times" id="closeSearch"></i>
            <div
              className="sidebar_toggle"
              title="Collapse sidebar"
              id="DashboardMainMenuID"
            >
              <a
                className="leftbarcollapestyle"
                id="collapselink"
                onClick={() => setIsOpen(!isOpen)}
              >
                <LuMenu className="icon-menu" />
              </a>
            </div> 
          </motion.div>
        </div>
     {
      breadCrumb != "" ? 
      <div className='hideInMobile'>      
      <div className='breadCrumbHead'>{breadCrumb}</div>
        {/* <div className='breadCrumbContainer'>
          <a href="https://nimbus.nimbusrms.com/Home" className='breadCrumbUrlText'>Home</a>
          <FaAngleDoubleRight />
          <span>{breadCrumb}</span>
      </div> */}
    </div>
      : <></>
    }
    <div className='exitIconContainer' onClick={handleLogOut}>
      <ImExit color='red' className='exitIcon' />
    </div>
    </>
  )
}